@import url(https://fonts.googleapis.com/css?family=Nunito);
.header{background-color:#558b2f;padding:0 30px;position:-webkit-sticky;position:sticky;display:flex;justify-content:space-between;align-items:center;height:60px;top:0}.header-link{list-style-type:none;color:#FFF;font-weight:bold;text-decoration:none;padding:0 20px;height:60px;display:flex;align-items:center}.header-link:hover{background-color:#66bb6a}.header-right{display:flex;align-items:center}.header-right>img:last-child{height:24px}.header-left{display:flex}.social-link{height:20px;padding:0 10px;cursor:pointer;display:block}.active{background-color:#66bb6a}

.button{display:inline-block;margin-top:20px;text-decoration:none;font-family:'Nunito',sans-serif;font-weight:bold;color:#494949;font-size:14px;text-align:center;padding:10px 15px;background:rgba(255,255,255,0.9);border:1px solid #494949;transition:all 0.4s ease 0s;border-radius:20px}.button:hover{color:#ffffff;background:#66BB6A;border-color:#66BB6A;transition:all 0.3s ease}

.cardContainer{padding:20px;padding-left:35px;border-radius:15px;display:flex;width:80%;margin:20px auto;min-height:350px}.cardText{width:60%;align-self:center;text-align:justify}.cardImage{width:40%;padding-left:5em;align-self:center}.cardImage>img{width:100%}

.cardTextContainer{display:flex;flex-direction:column;background-color:rgba(255,255,255,0.9);border-radius:15px;padding:40px;margin-left:15%;margin-right:15%;align-items:center}

.cardContainer{padding:20px;padding-left:35px;border-radius:15px;display:flex;width:80%;margin:20px auto;min-height:350px}.cardText{width:60%;align-self:center;text-align:justify}.cardImage{width:40%;padding-left:5em;align-self:center}.cardImage>img{width:100%}

body{background-color:#dcedc8;margin:0}h1,h2,p,ul,li,a{font-family:'Nunito', bold}*{margin:0;padding:0}.title{font-family:'Nunito', bold;color:var(--dos)}p{font-family:'Nunito', bold;font-size:14px}.mainRow{display:flex;justify-content:center;background:url(/compensate/static/media/forest-1.812266f6.jpg) center/cover;width:100%;height:calc(100vh - 60px);align-items:center}.card{background-color:rgba(255,255,255,0.9);border-radius:15px;padding:30px 20px;margin:0 15px;font-size:10px;text-align:center;width:30%;height:auto;max-width:290px}.cardListItem{display:flex;padding:12px;font-family:'Nunito', bold;align-items:center;margin-left:30px}.cardListItemAmount{margin-left:20px;margin-right:10px;font-family:'Nunito', bold;font-size:15px}.cardListItemText{font-family:'Nunito', bold;font-size:15px}.cardTitle{padding-bottom:5px;border-bottom:1px solid black;margin-bottom:20px}.rowContainer{justify-content:center;background:url(/compensate/static/media/forest-2.d611e140.jpg) center/cover}.row{padding:20px;display:flex}.rowCardImage{background-color:rgba(255,255,255,0.9);border-radius:15px;padding:20px;width:30%;display:flex;align-items:center;justify-content:center}

