@import url('https://fonts.googleapis.com/css?family=Nunito');
body {
    background-color: rgba(220,237,200,1);
    margin: 0;
}
h1, h2, p, ul, li, a {
    font-family: 'Nunito', bold;
}

* {
  margin: 0;
  padding: 0;
}

.title {
    font-family: 'Nunito', bold;
    color: var(--dos);
}

p {
    font-family: 'Nunito', bold;
    font-size: 14px;
}

.mainRow {
    display: flex;
    justify-content: center;
    background: url('images/forest-1.jpg') center/cover;
    width: 100%;
    height: calc(100vh - 60px);
    align-items: center;
}

.card {
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 15px;
    padding: 30px 20px;
    margin: 0 15px;
    font-size: 10px;
    text-align: center;
    width: 30%;
    height: auto;
    max-width: 290px;
}
.cardListItem {
    display: flex;
    padding: 12px;
    font-family: 'Nunito', bold;
    align-items: center;
    margin-left: 30px;
}
.cardListItemAmount {
    margin-left: 20px;
    margin-right: 10px;
    font-family: 'Nunito', bold;
    font-size: 15px;
}
.cardListItemText {
    font-family: 'Nunito', bold;
    font-size: 15px;
}
.cardTitle {
    padding-bottom: 5px;
    border-bottom: 1px solid black;
    margin-bottom: 20px;
}

.rowContainer {
  justify-content: center;
  background: url('images/forest-2.jpg') center/cover;
}

.row {
    padding: 20px;
    display: flex;
}

.rowCardImage {
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 15px;
    padding: 20px;
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
}
